<template>
	<div>
		<el-table :data="tableData" ref="multipleTable">
			<el-table-column label="名称" type="selection" align="center"></el-table-column>
			<el-table-column prop="id" label="用户ID" align="center"></el-table-column>
			<el-table-column prop="avatarUrl" label="头像" align="center">
				<template slot-scope="scope">
					<img :src="scope.row.avatarUrl" style="max-width:180px;max-height:80px;">
				</template>
			</el-table-column>
			<el-table-column prop="nickname" label="昵称" align="center"></el-table-column>
			<el-table-column prop="name" label="名称" align="center"></el-table-column>
			<el-table-column prop="sex" label="性别" align="center"></el-table-column>
			<el-table-column prop="phone" label="手机号码" align="center"></el-table-column>
			<el-table-column prop="secret_key" label="身份证" align="center"></el-table-column>
			<el-table-column label="操作" align="center" width="300px">
				<template slot-scope="scope">
					<el-button size="mini" type="primary">开通权限</el-button>
                    <el-button size="mini" type="primary">禁止使用权限</el-button>
				</template>
			</el-table-column>
		</el-table>
		
		<div class="block">
			<el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="prev, pager, next, jumper"
			 :total="totalPage">
			</el-pagination>
		</div>
	</div>
</template>

<script>
	import API from '@/api/index.js'
	export default {
		name: 'user',
        data() {
            return {
                tableData: [],
				currentPage: 1,
				totalPage: 0
            }
        },
		mounted() {
			this.getWxUser()
		},
		methods: {
			getWxUser() {
				API.wxUser(self.currentPage).then(res => {
					this.tableData = res.data
				})
			}
		}
	}
</script>

